@charset "UTF-8";
html {
  height: 100%;
}

body,
html {
  margin: 0;
  font-family: "Arial", "Helvetica", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  /* убирает пробелы, не дает переносить текст на другие строки */
  clip-path: inset(100%);
  /* определяет область отображения текста по шаблону. совместимость средняя */
  clip: rect(0 0 0 0);
  /* определяет область отображения текста по прямоугольнику. совместимость высокая */
  overflow: hidden;
  /* определяет что делать с элементами, которые не вмещаются в блок: показать/скрыть/скролл. можно применять к блокам, флексам и гридам */
}

a, ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.main-page {
  position: relative;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 19px;
  line-height: 23px;
  background: #ece8dd url("../../../src/images/fone.gif") left top repeat;
}

.hero__container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.main-page__hero-image {
  position: relative;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
  z-index: 2;
}

.hero {
  max-width: 1500px;
}

.hero__image {
  width: 100%;
  height: auto;
}

.hero__header {
  position: relative;
  margin: 0 auto;
  margin-bottom: 58px;
  z-index: 2;
}

.main-page-header {
  max-width: 1300px;
  display: flex;
  padding: 25px 75px;
  align-items: center;
  justify-content: space-between;
}

.user-block__avatar {
  padding-right: 25px;
}

.user-block {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.user-block__item {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: #e3e319;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.main-page__film-card {
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

.film-card {
  max-width: 1300px;
  padding: 25px 75px;
  display: flex;
}

.film-card__poster {
  margin-right: 40px;
}

.film-card__info {
  display: flex;
  flex-direction: column;
}

.film-card__image {
  width: 218px;
  max-width: 100%;
  height: auto;
}

.film-card__title {
  font-size: 32px;
  line-height: 45px;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  max-width: 750px;
  max-height: 140px;
  overflow: hidden;
}

.film-card__meta {
  font-size: 19px;
  line-height: 23px;
  font-weight: 700;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  margin-bottom: 25px;
}

.film-card__genre::after {
  content: "·";
  margin-left: 4px;
  margin-right: 4px;
}

.film-card__buttons {
  display: flex;
  max-width: 700px;
  max-height: 300px;
}

.film-card__button {
  margin-right: 14px;
}
.film-card__button:last-child {
  margin-right: 0px;
}

.button {
  display: flex;
  font-size: 19px;
  line-height: 23px;
  color: #ffff1b;
  background: rgba(55, 54, 41, 0.65);
  align-items: center;
  padding: 13px 30px;
  min-width: 128px;
  max-width: 300px;
  overflow: hidden;
  border-radius: 8px;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  will-change: transform;
  transition: 0.2s background-color, 0.2s transform, 0.2s -webkit-transform;
}
.button:hover {
  background: rgba(0, 0, 0, 0.51);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.button svg {
  vertical-align: top;
  margin-right: 9px;
}

.main-page__content {
  width: 1200px;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}

.main-page__footer {
  margin-top: auto;
  max-width: 1200px;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}

body {
  background-color: white;
}