.main-page {
  position: relative;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;

  display: flex;
  flex-direction: column;

  @include main-font-family;
  @include font-medium;

  background: #ece8dd url("../../../src/images/fone.gif") left top repeat;
}

.hero__container {
  position: absolute;
  top:0;
  left: 0;
  @include z-index-back;
}

.main-page__hero-image {
  position: relative;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
  @include z-index-middle;
}

.hero {
  max-width: 1500px;
}

.hero__image {
  width: 100%;
  height: auto;
}

.hero__header {
  position: relative;
  margin: 0 auto;
  margin-bottom: 58px;
  @include z-index-middle;
}

.main-page-header {
  max-width: 1300px;
  display: flex;
  padding: 25px 75px;
  align-items: center;
  justify-content: space-between;
}

.user-block__avatar {
  padding-right: 25px;
}

.user-block {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.user-block__item {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: $text-color-additional;
  @include black-text-stroke;

}

.main-page__film-card {
  position: relative;
  // margin-top: 100px;
  margin: 0 auto;
  @include z-index-middle;
}

.film-card{
  max-width: 1300px;
  padding: 25px 75px;
  display: flex;
}

.film-card__poster {
  margin-right: 40px;
}

.film-card__info {
  display: flex;
  flex-direction: column;
}

.film-card__image {
  width: 218px;
  max-width: 100%;
  height: auto;
}

.film-card__title {
  @include font-h2;
  color: $text-color-main;
  @include black-text-stroke;

  max-width: 750px;
  max-height: 140px;
  overflow: hidden;
}

.film-card__meta {
  @include font-medium;
  font-weight: 700;

  color: $text-color-main;
  @include black-text-stroke;

  margin-bottom: 25px;
}

.film-card__genre::after {
  content: '·';
  margin-left: 4px;
  margin-right: 4px;
}

.film-card__buttons {
  display: flex;
  max-width: 700px;
  max-height: 300px;
}

.film-card__button {
  margin-right: 14px;

  &:last-child {
    margin-right: 0px;
  }
}

.button {
  display: flex;

  @include font-medium;
  color: $text-color-main;
  
  background: rgba(55, 54, 41, 0.65);

  align-items: center;
  padding: 13px 30px;
  min-width: 128px;
  max-width: 300px;
  overflow: hidden;

  border-radius: 8px;
  border: 0;
  text-decoration: none;
  cursor: pointer;

  will-change: transform;
  transition: .2s background-color,.2s transform,.2s -webkit-transform;

  &:hover {
    background: rgba(0, 0, 0, 0.51);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  & svg {
    vertical-align: top;
    margin-right: 9px;
  }

}

.main-page__content {
  width: 1200px;
  // background-color: brown;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}

.main-page__footer {
  margin-top: auto;
  max-width: 1200px;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}